@import "~styles/common/normalize.scss";
@import "~styles/common/font_styles.scss";
@import "~styles/common/variables.scss";

/*начало общих стилей*/
.container {
  max-width: 1600px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  @media (max-width: 1640px) {
    padding: 0 30px;
  }
}

body {
  color: var(--light-color);
  font-size: 24px;
  background-color: var(--bg-gray);
  font-family: 'Onest-Regular', sans-serif;
  font-weight: 400;
  will-change: padding-right;

  a {
    color: var(--light-color);
    transition: all .3s;

    &:hover {
      color: var(--gray-color);
      border-bottom: 1px solid var(--gray-color);
      transition: all .3s;
    }
  }
}

h1 {
  font-size: 80px;
  line-height: 1.2;
  margin-bottom: 50px;
}

h1, h2, h3 {
  font-family: 'Inter-Medium', sans-serif;
  font-weight: 500;
}

.btn {
  display: inline-block;
  padding: 32px 50px;
  background-color: var(--light-color);
  color: var(--bg-gray);
  font-family: 'Inter-Medium', sans-serif;
  font-weight: 500;
  border-radius: 4px;
  text-transform: uppercase;
  text-align: center;
  transition: all .3s;

  &:hover {
    transition: all .3s;
    background-color: var(--gray-color);
    color: var(--bg-dark);
    border-bottom: none;
    box-shadow: var(--shadow);
  }

  &:focus{
    background-color: var(--bg-gray);
    color: var(--light-color);
    border: 1px solid var(--light-color);
  }
}

.no-scroll {
  overflow: hidden;
}

section {
  padding: 50px 0;
}

.title {
  margin-bottom: 50px;
  font-size: 54px;
  color: var(--support-color);
  line-height: 1.1;
}

.hover{
  &:hover{
    color: var(--menu-color);
    border-bottom: 1px solid var(--menu-color);
  }
}
/*Конец общих стилей*/

.header {
  padding: 25px 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
  transition: all .5s;
  background: rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(1px);

  &__logo  {
    max-width: 310px;
    width: 100%;
    height: 100%;
    overflow: hidden;
    a:hover {
      border-bottom: none;
    }

    a img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &.scroll {
    background: var(--bgc);
    backdrop-filter: var(--blur);
    padding: 18px 0;
    transition: all .5s;
    box-shadow: var(--shadow);

    .header__menu-item a {
      transition: background .3s;
    }
  }

  &__burger {
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 20px;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;

    span {
      display: block;
      width: 100%;
      height: 2px;
      background-color: var(--light-color);
      transition: transform 0.3s ease, opacity 0.3s ease;

    }

    &.active span:nth-child(1) {
      transform: rotate(45deg) translateY(8px) translateX(5px);
    }

    &.active span:nth-child(2) {
      opacity: 0;
    }

    &.active span:nth-child(3) {
      transform: rotate(-45deg) translateY(-8px) translateX(5px);
    }
  }

  &__inner {
    display: flex;
    gap: 200px;
    align-items: center;
  }

  &__menu-list {
    display: flex;
    gap: 30px;
  }

  &__menu-item {
    font-family: 'Inter-Medium', sans-serif;
    font-weight: 500;
    line-height: 1.5;
    font-size: 18px;

    a {
      text-transform: uppercase;
      color: var(--light-color);
      padding: 5px 10px;
      transition: background .3s;

      &:hover {
        border-bottom: 1px solid var(--gray-color);
        color: var(--gray-color);
      }
      &:active{
        border-bottom: 1px solid var(--light-color);
      }

      &.active{
        border-bottom: 1px solid var(--menu-color);
        color: var(--menu-color);
      }
    }
  }
}

@media (max-width: 1024px) {
  .header {
    &__inner {
      gap: unset;
      justify-content: space-between;
    }

    &__menu {
      position: fixed;
      will-change: transform, visibility, opacity;
      top: 86px;
      right: 0;
      width: 100%;
      height: calc(100vh - 86px);
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
      z-index: 999;
      transition: all 1s;
      visibility: hidden;
      opacity: 0;
      transform: translateX(100%);

      &.active {
        visibility: visible;
        opacity: 1;
        transform: translateX(0);
        backdrop-filter: var(--blur);
        background-color: rgba(0, 0, 0, 0.9);
      }
    }

    &__menu-list {
      flex-direction: column;
      gap: 15px;
      padding: 15px;
    }

    &__menu-item{
      font-size: 20px;
      a{
        display: block;
      }
    }

    &__burger {
      display: flex;
    }

    &.scroll {
      background: var(--bgc);
      backdrop-filter: var(--blur);
      padding: 18px 0;
      box-shadow: var(--shadow);
    }
  }
}

/*начало секции hero*/

.hero {
  background-color: var(--bg-dark);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: right;
  padding-top: 225px;

  &__inner {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 50px;
    margin-bottom: 50px;
    width: max-content;
    border-radius: 5px;
  }

  &__title,
  &__description,
  &__advantages {
    position: relative;
    max-width: 540px;
    width: 100%;
  }

  &__title {
    margin-bottom: 0;
  }

  &__description {
    font-size: 50px;
    line-height: 1.2;
  }

  &__advantages {
    display: flex;
    flex-direction: column;
    gap: 35px;
  }

  &__advantages-item {
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 39px;
      height: 39px;
      background: url('../images/svg/check.svg');
      background-size: contain;
      background-repeat: no-repeat;
    }

    p {
      background-color: rgba(40, 40, 41, 0.7);
      padding: 0 20px 0 56px;
      display: inline-block;
    }
  }

  &__advantages-num {
    font-size: 40px;
  }

  &__btn {
    position: relative;
    max-width: max-content;
  }
}

@media (max-width: 1320px) {
  .hero {
    &__inner {
      padding: 25px;
    }

    &__inner::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: var(--bgc);
      backdrop-filter: var(--blur);
    }

    &__advantages-item p {
      background-color: transparent;
    }
  }
}

@media (max-width: 1024px) {
  .hero {
    padding-top: 150px;

    h1 {
      font-size: 64px;
    }

    &__description {
      font-size: 36px;
    }

    &__advantages-num {
      font-size: 32px;
    }
  }
}

@media (max-width: 768px) {
  .hero {
    padding-top: 120px;
    background-size: cover;
    background-position: center;

    &__inner {
      gap: 30px;
      width: 100%;
    }

    &__advantages {
      gap: 20px;
    }

    &__advantages-item:before {
      width: 30px;
      height: 30px;
    }

    &__advantages-item p {
      padding: 0 20px 0 45px;
    }

    h1 {
      font-size: 48px;
    }

    &__description {
      font-size: 32px;
    }
  }
}

@media (max-width: 468px) {
  .hero {
    h1 {
      font-size: 40px;
    }

    &__inner {
      padding: 15px;
      gap: 25px;
    }

    &__description {
      font-size: 24px;
    }

    &__advantages-item p {
      padding: 0 15px 0 40px;
    }

    &__advantages-num {
      font-size: 24px;
    }

    &__advantages-item:before {
      width: 24px;
      height: 24px;
    }
  }
}

/*конец секции hero*/

/*начало секции advantages*/
.advantages {
  padding-top: 100px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: end;

  &__image--left,
  &__image--right {
    width: 100%;
    max-height: 725px;
    height: 100%;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__content {
    padding: 50px 50px 0;
  }

  &__content-list {
    display: flex;
    flex-direction: column;
    gap: 80px;
  }

  &__content-item {
    line-height: 1.4;
    position: relative;
    padding-left: 78px;

    &:before {
      content: '';
      position: absolute;
      width: 58px;
      height: 47px;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background: url('../images/svg/exclude.svg') no-repeat top / contain;
    }

    &:after {
      content: '';
      position: absolute;
      height: 1px;
      width: 100%;
      top: calc(100% + 40px);
      left: 0;
      background-color: #55565A;
      transition: background-color .3s;
    }

    &:hover::after {
      background-color: var(--light-color);
      transition: background-color .3s;
    }
  }
}

@media (max-width: 1670px) {
  .advantages {
    &__content {
      padding: 50px 35px 0;
    }

    &__content-list {
      gap: 50px;
    }

    &__content-item {
      &:after {
        top: calc(100% + 25px);
      }
    }
  }
}

@media (max-width: 1200px) {
  .advantages {
    grid-template-columns: 1fr 1fr;
    gap: 50px;

    & > :nth-child(3n + 3) {
      grid-column: span 2;
    }

    &__content {
      padding: 50px 35px 0 0;
    }
  }
}

@media (max-width: 920px) {
  .advantages {
    grid-template-columns: 1fr;

    & > :nth-child(3n + 3) {
      grid-column: span 1;
    }

    &__content {
      padding: 15px 30px;
    }
  }
}

@media (max-width: 768px) {
  .advantages {
    &__image--left,
    &__image--right {
      max-height: 500px;
    }
  }
}

@media (max-width: 468px) {
  .advantages {
    padding-top: 50px;

    &__content {
      padding: 0 15px;
    }

    &__content-item {
      padding-left: 60px;

      &:before {
        width: 40px;
        height: 40px;
      }
    }
  }
}

/*конец секции advantages*/

/*начало секции с формой*/

.form-section {
  padding-bottom: 100px;

  .error-message {
    color: red;
    margin-top: 10px;
    opacity: 0;
    height: 30px;
    transform: translateY(-10px);
    transition: opacity 0.3s ease, transform 0.3s ease;
    visibility: hidden;
  }

  .error-message.show {
    opacity: 1;
    transform: translateY(0);
    visibility: visible;
  }

  &__container {
    position: relative;
    background-color: var(--light-color);
    background-repeat: no-repeat;
    background-position: right;
    background-size: contain;
    padding: 72px 135px;
    border-radius: 30px;
    display: flex;
    align-items: start;
    gap: 30px;
    clip-path: inset(-100% 0 0 0);
  }

  form {
    display: flex;
    flex-direction: column;
    gap: 12px;

    a {
      color: var(--support-color);
      text-decoration: underline;

      &:hover{
        border-bottom: none;
        color: var(--bg-dark);
      }
    }

    input[type="text"],
    input[type="email"] {
      line-height: 1.5;
      color: var(--bg-dark);
      border: 2px solid #d6d6d6;
      border-radius: 4px;
      padding: 14px 14px 14px 38px;

      &:focus{
        border: 2px solid var(--bg-gray);
      }
    }

    label {
      margin: 12px 0;
      color: var(--support-color);
      line-height: 1.3;
      font-size: 20px;
    }

    input[type="checkbox"] {
      width: 24px;
      height: 24px;
      margin-right: 10px;
      position: relative;
      top: 5px;
    }

    button {
      background-color: var(--bg-gray);
      color: var(--light-color);

      &:active{
        border: none;
        transform: scale(1);
      }

    }
  }

  &__left {
    max-width: 780px;
    width: 100%;
    position: relative;
    z-index: 10;
  }

  &__right {
    position: absolute;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    height: 115%;
    bottom: -5px;
    right: 0;
    width: 50%;
  }
}

@media (max-width: 1670px) {
  .form-section {
    &__left {
      max-width: 525px;
    }

    &__container {
      padding: 72px 80px;
    }

    &__right {
      width: 100%;
      background-position: right;
    }
  }
}

@media (max-width: 1200px) {
  .form-section {
    &__left {
      max-width: 480px;
    }
  }
}

@media (max-width: 1024px) {
  .form-section {
    &__container{
      flex-direction: column;
      background-image: none !important;
    }

    &__left {
      max-width: 70%;
    }

    &__right {
      max-width: 50%;
      width: 100%;
      bottom: -20%;
      height: 100%;
    }
  }
}

@media (max-width: 868px) {
  .form-section {
    padding-bottom: 50px;

    form {
      gap: 20px;
    }

    &__container {
      padding: 25px 25px 0;
      gap: 50px;
    }

    &__left {
      max-width: 100%;
    }

    &__right {
      position: relative;
      background-position: center;
      max-width: 100%;
      height: 450px;
      bottom: -15px;
    }
  }
}

@media (max-width: 468px) {
  .form-section {
    &__container {
      gap: 30px;
    }

    &__right {
      height: 320px;
    }

    form {
      input[type="text"],
      input[type="email"] {
        padding: 14px 14px 14px 20px;
      }
    }
  }
}

/*конец секции с формой*/

/*начало футера*/

.footer {
  background-color: var(--bg-dark);

  &__top {
    padding: 40px 0;
    border: 1px solid var(--gray-color);
    border-left: none;
    border-right: none;
  }

  &__top-menu {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__top-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 60px;
  }

  &__top-item {
    text-transform: uppercase;
  }

  &__bottom {
    padding: 50px 0;

    .container {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 40px;
      align-items: center;

      & > :nth-child(3n + 2) {
        justify-self: center;
      }

      & > :nth-child(3n) {
        justify-self: end;
      }
    }
  }

  &__bottom-socials {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 30px;
  }

  &__bottom-link {
    display: block;
    width: 100%;
    transition: transform .3s;

    &:hover {
      border-bottom: none;
      transform: scale(1.1);
      transition: transform .3s;
    }
  }

  &__bottom-logo {
    max-width: 310px;
    width: 100%;
    height: 100%;
    overflow: hidden;
    a:hover {
      border-bottom: none;
    }
    a img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__bottom-btn {
    width: max-content;
    padding: 16px 40px;
  }

  &__link {
    /*display: block;*/
    padding: 10px 0;
  }
}

@media (max-width: 1024px) {
  .footer {
    &__bottom {
      .container {
        grid-template-columns: repeat(2, 1fr);

        & > :nth-child(odd) {
          justify-self: start;
        }

        & > :nth-child(even) {
          justify-self: end;
        }
      }
    }
  }
}

@media (max-width: 768px) {
  .footer {
    &__top-list {
      flex-direction: column;
      align-items: center;
    }

    &__bottom {
      .container {
        grid-template-columns: 1fr;

        & > :nth-child(odd) {
          justify-self: center;
        }

        & > :nth-child(even) {
          justify-self: center;
        }
      }
    }

    &__link {
      max-width: 100%;
      width: 100%;
      text-align: center;
    }
  }
}

@media (max-width: 468px) {
  .footer {
    &__top-list {
      gap: 40px;
    }

    &__top {
      padding: 25px 0;
    }

    &__top-item a {
      display: block;
    }

    &__bottom {
      padding: 25px 0;
    }
  }
}

/*конец футера*/

@media (max-width: 1024px) {
  .btn {
    padding: 18px 50px;
  }
}

@media (max-width: 768px) {
  .btn {
    padding: 14px 36px;
  }
}

@media (max-width: 468px) {
  body {
    font-size: 18px !important;
  }
  h2.title {
    font-size: 36px;
    margin-bottom: 30px;
  }
  section {
    padding: 25px 0;
  }
  .container {
    padding: 0 15px;
  }
  .btn {
    font-size: 20px;
  }
}